.card-img {
  width: 100%;
  height: 100%;
  aspect-ratio: 16/9;
  object-fit: cover;
  object-position: bottom;
}

.card-img :after {
  background-color: aqua;
}

.card-img:hover {
  /* position: relative; */
  background-color: black;
}

.card-title {
  font-size: 1.1rem;
}

.card {
  display: flex;
  margin: 10px;
  background-color: #fff;
  justify-content: center;
  min-width: 0;
  flex-direction: column;
  background-clip: border-box;
  /* border-radius: 0.25rem; */
}
